<script>
  import DeliveryModalPickup from "../components/DeliveryModalPickup";
  import DroneDeliveryMenu from "../mixins/DroneDeliveryMenu";
  import GridFoodItem from "../components/GridFoodItem";
  import BaseButton from "../components/BaseButton";
  import CartItem from "../components/CartItem";
  import BaseModal from "../components/BaseModal";
  import axios from 'axios';
  import changeLanguage from "@/util/changeLanguage";

  export default {
    name: "ViewDroneDeliveries",
    components: {
      GridFoodItem,
      BaseButton,
      DeliveryModalPickup,
      CartItem,
      BaseModal
    },
    data() {
      return {
        menu: DroneDeliveryMenu,
        menuVisible: false,
        point: null,
        delivery_points: [],
        isModalVisible: false,
      };
    },
    mounted() {
      // https://stackoverflow.com/questions/30082277/accessing-a-new-style-public-google-sheet-as-json
      let gapikey = 'AIzaSyBLaIhBi5GtkC3VZ2VU59m6WtYGv6ehA54'; //TODO: Move to config
      let sheetid = '1vT6AkLrMVbmmRrs98TGQxjhCtvJGkPIs1WAD93rP4B8';
      const sheetsUrl = 'https://sheets.googleapis.com/v4/spreadsheets/' + sheetid + '/values/Sheet1!A1:I16?key=' + gapikey;
      axios.get(sheetsUrl).then(res => {
        let data = res.data.values;

        data = data.slice(1).map(function(xs) {
          data[0]['icon'] = 'icon';
          return xs.reduce(function(acc, x, i) {
            acc[data[0][i]] = x
            return acc
          },{})
        })
        data = data.filter(function(d) {
          return d.active && d.delivery_point_id !== '';
        });
        this.delivery_points = data;
        this.isModalVisible = true;
      });
    },
    computed: {
      parentProductId: function () {
        if (this.dronecartItems.length > 0) {
          return this.dronecartItems[0].parent_product_id;
        } else {
          return 0;
        }
      },
      cartWeight: function () {
        let self = this;
        let cartItems = this.$store.state.cartItems;
        let total = 0;
        cartItems.forEach(function (currentValue) {
          Object.values(self.menu).forEach(function (r) {
            Object.values(r.items).forEach(function (o) {
              if (o.entity_id == currentValue.product_id) {
                if (typeof (currentValue.weight == 'undefined') || currentValue.weight == 0) {
                  total = total + (1.25 * currentValue.qty);
                } else {
                  total = total + parseFloat(currentValue.weight * currentValue.qty);
                }
              }
            })
          })
        })
        return total;
      },
      dronecartItems: function () {
        let self = this;
        let cartItems = this.$store.state.cartItems;
        let droneitems = [];
        cartItems.filter(function (item, index) {
          Object.values(self.menu).forEach(function (r) {
            Object.values(r.items).forEach(function (o) {
              if (o.entity_id == item.product_id) {
              droneitems.push(item); // TODO: Fix - hacking in a hurry - filter should handle this
              return true;
              }
              return false;
            })
          })
        });
        return droneitems;
      }
    },
    methods: {
      hideModal() {
        this.isModalVisible = false;
      },
      checkout() {
        // Check if shipment item is in cart
        let found = false;
        let droneDeliveryProductId = 83967;
        let cartItems = this.$store.state.cartItems;
        cartItems.forEach(function (item) {
          if (item.entity_id == droneDeliveryProductId) {
          found = true;
          window.location.href = 'https://www.aha.is/checkout/onepage';
          return;
          }
        })
        if (!found) {
          // We need to add this to cart our selves
          let data = {
            "product": droneDeliveryProductId,
            "super_product": this.parentProductId,
            "options": {
              "2398956": "3369810",
              "2495326": this.point.delivery_point_id
            }
          }
          this.$store.dispatch('addToCart', data).then(res => {
            window.location.href = 'https://www.aha.is/checkout/onepage';
          })
        }
      // If not add item to cart
      // Go to checkout
      },
      confirmPoint(point) {
        this.point = point;
        this.menuVisible = true;
        this.isModalVisible = false;
      },
      hideMenu() {
        this.menuVisible = false;
      },
      chunk: (arr, chunkSize = 1, cache = []) => {
        const tmp = [...arr]
        if (chunkSize <= 0) return cache
        while (tmp.length) cache.push(tmp.splice(0, chunkSize))
        return cache
      },
      toggleLanguage() {
        changeLanguage(this.$i18n.locale === "en" ? "is" : "en")
      },
      changePoint() {
        this.point = null;
        this.isModalVisible = false;
      },
    },
  }
</script>

