<template>
  <BaseModal
    :isVisible="isSelectPickupModalVisible"
    :centeredTitle="true"
    :noFooter="true"
    @hide="toggleSelectPickupModal"
  >
    <PickupListSelect
      :selectPickupPoint="select"
      :selectedNettoAddress="tempAddress"
    />

  </BaseModal>
</template>


<script>
  import { METHOD_PICKUP } from '@/util/constants';
  import BaseModal from '@/components/BaseModal';
  import PickupListSelect from '@/components/PickupListSelect';

  export default {
    name: "DeliveryModalPickup",
    components: {
      BaseModal,
      PickupListSelect,
    },
    props: {
      isSelectPickupModalVisible: Boolean,
      toggleSelectPickupModal: Function,
      changeAddress: Function,
    },
    data() {
      return {
        selected: undefined,
        tempAddress: null,
      };
    },
    methods: {
      select(point) {
        this.selected = point.name;
        this.tempAddress = {
            lat: point.latitude,
            lon: point.longitude,
            postcode: null,
            street: point.name,
            method: METHOD_PICKUP,
            id: point.id,
        }

        this.confirmAddress()
      },
      confirmAddress(){
        this.changeAddress(this.tempAddress, 'pickup')
        this.toggleSelectPickupModal()
      }
    },
  }
</script>


